@media (max-width: 800px) {
#app {
    width: 100%;
    margin: 0;
}
.welcome {
    padding: 16px;
}
.help {
    padding: 0 8px;
}
.about-line {
    bottom: 0;
    top: initial;
    right: 0;
}
}
@media (max-height: 550px) {
